import Vue from "vue";

import Vuex from "vuex";
import axios from "axios";
import webConfig from "@/data/config";

Vue.use(Vuex);

const user = {
  namespaced: true,
  state: {
    userForm: {
      id: "",
      account: "",
      password: "",
      email: "",
      phone: "",
      avatar: "",
    },
  },
};

const register = {
  namespaced: true,
  state: {
    rDV: false,
    address: "",
    registerState: false,
  },
  mutations: {
    changeRegisterDialog(state) {
      state.rDV = !state.rDV;
    },
    checkRegister(state, value) {
      if (value.data.code == 200) {
        state.registerState = true;
      }
    },
  },
  actions: {
    checkRegister(context, { account, password, phone, email }) {
      axios
        .post(`${webConfig.url}/usr/registry`, {
          UsrDTO: {
            token: "",
            usr: {
              usrAccount: account,
              usrEmail: email,
              usrPhone: phone,
              usrPwd: password,
            },
          },
        })
        .then((value) => {
          context.commit("checkRegister", value);
        });
    },
  },
  getters: {},
};

const login = {
  namespaced: true,
  state: {
    lDV: false,
    fDV: false,
    address: "",
    loginState: false,
  },
  mutations: {
    changeLoginDialog(state) {
      setTimeout(() => {
        state.lDV = !state.lDV;
      }, 300);
    },
    changeFoundDialog(state) {
      setTimeout(() => {
        state.fDV = !state.fDV;
      }, 300);
    },
    checkLogin(state, value) {
      if (value.data.code == 200) {
        state.loginState = true;
      }
    },
  },
  actions: {
    checkLogin(context, { usrAccount, usrPwd }) {
      axios({
        url: `${webConfig.url}/usr/login`,
        params: { usrAccount, usrPwd },
      }).then((value) => {
        context.commit("checkLogin", value);
      });
    },
  },
  getters: {},
};
const plane = {
  namespaced: true,
  state: {},
};
export default new Vuex.Store({
  modules: {
    user,
    register,
    login,
    plane,
  },
});
